body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* form serach style */
.hiddenText{
  display: none;
}
.modal-dialogs {
  z-index: 1003;
  max-height: 88%;
  top: 0;
  bottom: 0;
  margin: 0;
  width: 100%;
  max-height: 45%;
  border-radius: 0;
  position: fixed;
  left: 0;
  right: 0;
  max-height: 100px;
}
.modal-dialogs-large{
  position: absolute;
  max-height: 70%;
  width: 500px;
  margin: auto;
  border-radius: 20px;
  z-index: 1003;
  max-height: 88%;
}
.modal-dialogs-large:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-left: 11px solid transparent;
  border-right: 11px solid transparent;
  border-bottom: 11px solid rgb(68, 138, 255);
  margin-left: 30px;
}
.modal-dialogs-content{
  padding: 24px;
}
.modal-overlay-large {
  z-index: 1000;
  position: fixed;
  z-index: 999;
  top: -25%;
  left: 0;
  bottom: 0;
  right: 0;
  height: 125%;
  width: 100%;
  background: #000;
  display: none;
  will-change: opacity;
}
.Cal__Header__dateWrapper.Cal__Header__day,
.Cal__Header__dateWrapper.Cal__Header__day {
  height: 18px;
  font-size: 18px;
  line-height: 18px;
}
.Cal__Header__date{
  font-size: 16px;
}
div.Cal__Header__root {
  min-height: 78px;
  padding: 10px 15px;
}
.calendar-ok-btn{
  bottom: 0;
  position: fixed;
}
.material-icons {
  vertical-align: middle;
}
.white-box{
  background-color: #fff !important;
  padding: 10px 0;
  cursor: pointer;
}
.date-title{
  font-size: 120%;
  line-height: 30px;
}
.App .btn-submit {
  height: 100%;
  line-height: 80px;
  font-size: 120%;
}
header{
  background-color: blue;
  background-size: cover;
  background-position: center;
  min-height: 300px;
}
@media screen and (max-width: 670px){
  header{
    min-height: 500px;
  }
}
.section{
  padding-top: 4vw;
  padding-bottom: 4vw;
}
.tabs .indicator{
  background-color: #1a237e;
}
.tabs .tab a:focus, .tabs .tab a:focus.active{
  background: transparent;
}
.submited-search {
  background-color: rgba(0,0,0,.36);
  padding: 16px 20px;
  color: #dcd6d6;
}